<template>
    <v-container fluid>
        <div v-if="blogArticles.length===0 && !loading">
            <EmptyArticles/>
        </div>
        <div v-else>
            <v-card-title class="d-block text-center">最新文章</v-card-title>
            <ArticlePreview @transferCurrentPage="getCurrentPage" :blog-articles="blogArticles"
                            :total-page="totalPages" :loading="loading"/>
        </div>
    </v-container>
</template>

<script>
    import ArticlePreview from "@/components/ArticlePreview"
    import EmptyArticles from "@/components/constant/EmptyArticles"

    export default {
        name: "Home",
        components: {
            EmptyArticles,
            ArticlePreview
        },
        data: function () {
            return {
                blogArticles: [],
                totalPages: 0,
                loading: Boolean
            }
        },
        methods: {
            getArticles(currentPage) {
                this.loading = true
                let _this = this
                this.axios.get(this.API.URL.QUERY_ARTICLE + "&page=" + (currentPage - 1), {headers: {'Content-Type': 'multipart/form-data'}})
                    .then(function (result) {
                        _this.loading = false
                        let data = result.data
                        if (data.success) {
                            // console.log(data)
                            _this.totalPages = data.data.totalPages
                            _this.blogArticles = data.data.data
                            // console.log("_this.tags", _this.tags)
                        }
                    }, () => {
                        _this.loading = false
                    })
            },
            getCurrentPage(currentPage) {
                this.getArticles(currentPage)
                // console.log("从ArticlePreview传过来的当前page为", currentPage)
            }
        },
        created() {
            this.getArticles(1)
        }
    }
</script>

